// $lib/db/indexeddb.js
const DB_NAME = "App2KSecureDB";
const STORE_NAME = "authStore";
const KEY_STORE = "cryptoKeys";
const TOKEN_KEY = "refreshToken_encrypted";
const CRYPTO_KEY_ID = "aes-key";

let cryptoKey; // cached AES-GCM key

// ==============================="
// Open DB & create 2 stores
// ===============================
function openDB() {
    return new Promise((resolve, reject) => {
        const req = indexedDB.open(DB_NAME, 3); // tng version ln 3  chc chn upgrade nu cn

        req.onupgradeneeded = () => {
            const db = req.result;

            if (!db.objectStoreNames.contains(STORE_NAME)) {
                db.createObjectStore(STORE_NAME);
            }
            if (!db.objectStoreNames.contains(KEY_STORE)) {
                db.createObjectStore(KEY_STORE);
            }
        };

        req.onsuccess = () => resolve(req.result);
        req.onerror = () => reject(req.error);
        req.onblocked = () => reject(new Error("Database blocked"));
    });
}

// ===============================
// Save exported raw key (ArrayBuffer)
// ===============================
async function saveExportedKey(keyRaw) {
    const db = await openDB();
    const tx = db.transaction(KEY_STORE, "readwrite");
    tx.objectStore(KEY_STORE).put(keyRaw, CRYPTO_KEY_ID);
    return new Promise((resolve, reject) => {
        tx.oncomplete = resolve;
        tx.onerror = reject;
    });
}

// ===============================
// Load exported raw key
// ===============================
async function loadExportedKey() {
    const db = await openDB();
    const tx = db.transaction(KEY_STORE, "readonly");
    const req = tx.objectStore(KEY_STORE).get(CRYPTO_KEY_ID);

    return new Promise((resolve) => {
        req.onsuccess = () => {
            const result = req.result;
            // result c th l ArrayBuffer hoc undefined
            resolve(result instanceof ArrayBuffer ? result : null);
        };
        req.onerror = () => resolve(null);
    });
}

// ===============================
// Get or Create Crypto Key (cached)
// ===============================
async function getCryptoKey() {
    if (cryptoKey) return cryptoKey;

    const exported = await loadExportedKey();

    if (exported) {
        cryptoKey = await crypto.subtle.importKey(
            "raw",
            exported,
            { name: "AES-GCM" },
            true,
            ["encrypt", "decrypt"]
        );
    } else {
        cryptoKey = await crypto.subtle.generateKey(
            { name: "AES-GCM", length: 256 },
            true,
            ["encrypt", "decrypt"]
        );
        const raw = await crypto.subtle.exportKey("raw", cryptoKey);
        await saveExportedKey(raw);
    }

    return cryptoKey;
}

// ===============================
// Encrypt  tr v ArrayBuffer (an ton cho mi browser)
// ===============================
async function encrypt(value) {
    const key = await getCryptoKey();
    const iv = crypto.getRandomValues(new Uint8Array(12));
    const encoded = new TextEncoder().encode(value);

    const cipherBuffer = await crypto.subtle.encrypt(
        { name: "AES-GCM", iv },
        key,
        encoded
    );

    return {
        cipher: cipherBuffer,       // ArrayBuffer
        iv: iv.buffer               // ArrayBuffer (12 bytes)
    };
}

// ===============================
// Decrypt
// ===============================
async function decrypt(cipherBuffer, ivBuffer) {
    const key = await getCryptoKey();

    // m bo 100% l Uint8Array trc khi decrypt
    const cipherArr = cipherBuffer instanceof ArrayBuffer ? new Uint8Array(cipherBuffer) : cipherBuffer;
    const ivArr = ivBuffer instanceof ArrayBuffer ? new Uint8Array(ivBuffer) : ivBuffer;

    const decodedBuffer = await crypto.subtle.decrypt(
        { name: "AES-GCM", iv: ivArr },
        key,
        cipherArr
    );

    return new TextDecoder().decode(decodedBuffer);
}

// ===============================
// Save Refresh Token (encrypted)
// ===============================
export async function saveRefreshToken(token) {
    if (!token) return false;

    const encrypted = await encrypt(token);

    const db = await openDB();
    const tx = db.transaction(STORE_NAME, "readwrite");
    const store = tx.objectStore(STORE_NAME);

    store.put({
        cipher: encrypted.cipher,   // ArrayBuffer
        iv: encrypted.iv            // ArrayBuffer
    }, TOKEN_KEY);

    return new Promise((resolve, reject) => {
        tx.oncomplete = () => resolve(true);
        tx.onerror = () => reject(tx.error);
    });
}

// ===============================
// Get Refresh Token (decrypt)
// ===============================
export async function getRefreshToken() {
    const db = await openDB();
    const tx = db.transaction(STORE_NAME, "readonly");
    const req = tx.objectStore(STORE_NAME).get(TOKEN_KEY);

    return new Promise((resolve) => {
        req.onsuccess = async () => {
            if (!req.result) {
                resolve(null);
                return;
            }

            const { cipher, iv } = req.result;

            try {
                const token = await decrypt(cipher, iv);
                resolve(token);
            } catch (err) {
                console.warn("Token decrypt failed  clearing storage", err);
                await clearRefreshToken(); // xa key hng
                resolve(null);
            }
        };

        req.onerror = () => {
            console.error("IndexedDB get error:", req.error);
            resolve(null);
        };
    });
}

// ===============================
// Clear everything (token + key)
// ===============================
export async function clearRefreshToken() {
    const db = await openDB();

    const tx1 = db.transaction(STORE_NAME, "readwrite");
    tx1.objectStore(STORE_NAME).delete(TOKEN_KEY);

    const tx2 = db.transaction(KEY_STORE, "readwrite");
    tx2.objectStore(KEY_STORE).delete(CRYPTO_KEY_ID);

    return Promise.all([
        new Promise(res => { tx1.oncomplete = res; tx1.onerror = res; }),
        new Promise(res => { tx2.oncomplete = res; tx2.onerror = res; })
    ]);
}

// ===============================
// [TY CHN] Xa ton b DB  dng khi test
// ===============================
export async function nukeDBForTesting() {
    return new Promise((resolve, reject) => {
        const req = indexedDB.deleteDatabase(DB_NAME);
        req.onsuccess = () => {
            console.log("DB deleted successfully");
            resolve();
        };
        req.onerror = () => reject(req.error);
        req.onblocked = () => {
            alert("Close all tabs using this site then try again");
        };
    });
}