<script>
  export let user; // { id, name, lastMessageAt, lastMessagePreview, online }
  export let selectedId; // id user ang chn
  export let onSelect = () => {}; // callback chn user

  // Format thi gian
  function formatTime(t) {
    if (!t) return '';
    return new Date(t).toLocaleTimeString([], {
      hour: '2-digit',
      minute: '2-digit'
    });
  }
</script>

<div
  on:click={() => onSelect(user)}
  class="
    p-2 rounded-md mb-1 cursor-pointer transition select-none relative
    flex items-center gap-3 border border-transparent
    hover:bg-blue-50 active:scale-[0.98]
    {selectedId === user.id ? 'bg-blue-100 border-blue-300 shadow-sm' : ''}
  ">
  <!-- Avatar -->
  <div
    class="
      w-10 h-10 rounded-full flex items-center justify-center
      bg-gray-200 text-gray-600 font-semibold text-lg uppercase
    ">
    {user.name?.charAt(0) ?? '?'}
  </div>

  <!-- Content -->
  <div class="flex-1 min-w-0">
    <!-- Top row: name + time -->
    <div class="flex items-center justify-between">
      <span class="font-medium text-gray-800 truncate">
        {user.name}
      </span>

      {#if user.lastMessageAt}
        <span class="text-[11px] text-gray-400 whitespace-nowrap">
          {formatTime(user.lastMessageAt)}
        </span>
      {/if}
    </div>

    <!-- Message preview -->
    <div class="text-[12px] text-gray-500 truncate mt-0.5 text-right">
      {user.lastMessagePreview || 'No messages yet'}
    </div>
  </div>

  <!-- Status dot -->
  <div
    class="
      w-3 h-3 rounded-full absolute left-2 bottom-2
      {user.online ? 'bg-green-500' : 'bg-gray-300'}
    ">
  </div>
</div>
