<script>
  import { onMount, afterUpdate } from 'svelte';
  import {
    connectWebSocket,
    sendMessage,
    loadMessagesWS,
    users,
    messages,
    status,
    myId,
    myName
  } from '$lib/websocket-client.js';
  import { theme, toggleTheme } from '$lib/theme.js';

  import Message from '@components/Message.svelte';
  import Conversion from '@components/Conversion.svelte';
  import { mgss } from '$lib/messages-sample';

  $: console.log('$users:', $users);

  let input = '';
  let selected = null; // user ID ang chat
  let chatBox; // div  scroll vo cui

  // ===============================
  // 1) Khi chn user  load messages
  // ===============================
  function selectUser(u) {
    selected = u.id;
    if (selected == 103) {
      messages.set(mgss);
      return;
    }
    messages.set([]); // xa tin nhn c
    loadMessagesWS(u.id); // gi WS load 50 tin cui
  }

  // ===============================
  // 2) Gi tin nhn
  // ===============================
  function send() {
    if (!input.trim() || !selected) return;

    sendMessage({
      type: 'chat',
      to: selected,
      message: input.trim()
    });

    input = '';
  }

  // ===============================
  // 3) Khi messages thay i  scroll bottom
  // ===============================
  afterUpdate(() => {
    if (chatBox) {
      chatBox.scrollTop = chatBox.scrollHeight;
    }
  });

  onMount(() => {
    //connectWebSocket();
  });
</script>

<!-- Dark Mode Wrapper -->
<main
  class="flex h-screen transition-colors duration-300
             bg-gray-100 text-gray-800
            ">
  <!-- SIDEBAR -->
  <aside
    class="w-52 border-r p-3 flex flex-col gap-2 shadow-sm
                bg-white">
    <!-- Header -->
    <p class="text-sm font-medium mb-1 flex items-center gap-1">
      <span class="font-semibold text-blue-600 truncate">{$myName},</span>

      <span
        class="text-[10px] px-2 py-[1px] rounded-full
               {$status.includes('Connected') ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600 '}">
        {$status.replace(/\s.*$/, '')}
      </span>
    </p>

    <!-- User List -->
    {#each $users as user}
      <Conversion {user} onSelect={selectUser} selectedId={selected} />
    {/each}
  </aside>

  <!-- CHAT AREA -->
  <div class="flex-1 flex flex-col">
    <div
      bind:this={chatBox}
      class="flex-1 border-b p-3 overflow-y-auto
            border-gray-200">
      {#if selected === null}
        <p class="text-center text-gray-400 mt-5">Select a user to start chatting.</p>
      {:else}
        {#each $messages as msg (msg.id)}
          <Message {msg} myId={$myId} />
        {/each}
      {/if}
    </div>

    <!-- Input -->
    <form on:submit|preventDefault={send} class="flex p-3 gap-2 bg-white">
      <input
        bind:value={input}
        placeholder="Type message..."
        class="flex-grow rounded px-3 py-2 border
               border-gray-300
               bg-white
               text-gray-800
               placeholder-gray-400
               focus:outline-none focus:ring-2 focus:ring-blue-400" />

      <button type="submit" class="px-4 py-2 rounded text-white bg-blue-500 hover:bg-blue-600 transition">Send</button>
    </form>
  </div>
</main>
